
import { storeAccessToken, storeRefreshToken } from '@/utils/storage.util'
import { fetchUserProfile } from '@/utils/users.util'
import { Component, Vue } from 'vue-property-decorator'
import api from '../services/api'
interface FormValues {
  email: string;
  password: string;
}

  @Component
export default class Login extends Vue {
  formValues: FormValues = {
    email: '',
    password: ''
  }

  loading = false

  checkValidity (): boolean {
    return !(this.formValues.email && this.formValues.password) || this.loading
  }

  handleInput (event: Event): void {
    const { name, value } = event.target as HTMLInputElement
    this.formValues = { ...this.formValues, [name]: value }
  }

  login (e: Event): void {
    e.preventDefault()
    const payload = {
      email: this.formValues.email,
      password: this.formValues.password
    }
    this.loading = true
    api.post('token/', payload)
      .then((data) => {
        this.loading = false
        storeAccessToken(data.data.access)
        storeRefreshToken(data.data.refresh)
        const tokenPayload = window.atob(data.data.access?.split('.')[1] || '')
        const { user_id: user } = JSON.parse(tokenPayload)
        return fetchUserProfile(user)
      })
      .then(() => {
        this.$router.push('/dashboard')
      })
      .catch(({ response, message }) => {
        this.loading = false
        if (!response) return alert(message)
        this.formValues.password = ''
        alert('invalid email or password')
      })
  }

  mounted (): void {
    const token = localStorage.getItem('ACCESS_TOKEN')
    if (token) this.$router.push('/dashboard')
  }
}
